'use client';
import React, { useState } from 'react';
import Sidebar from './sidebar';
import GoogleMapsComponent from './map';

// Define the Property interface here (or import it from a types file)
interface Property {
  id: number;
  address: string;
  price: number;
  latitude: number;
  longitude: number;
  description: string;
}

export default function Home() {
  const [isPinMode, setIsPinMode] = useState<boolean>(false);
  const [isConfirmMode, setIsConfirmMode] = useState<boolean>(false);
  const [hasActivePin, setHasActivePin] = useState<boolean>(false);
  const [radius, setRadius] = useState<number>(5);
  
  // 1. New State for Search & Results with strict TypeScript types
  const [searchLocation, setSearchLocation] = useState<{lat: number, lng: number} | null>(null);
  const [properties, setProperties] = useState<Property[]>([]); 

  const handleConfirm = () => {
    setIsConfirmMode(false);
    console.log("Location confirmed! Searching database...");

    if (searchLocation) {
        // 2. FETCH FROM SPRING BOOT
        // We convert miles (radius) to KM for the backend (1 mile = 1.609 km)
        const radiusKm = radius * 1.609;
        
        fetch(`http://localhost:8080/search?lat=${searchLocation.lat}&lon=${searchLocation.lng}&radius=${radiusKm}`)
            .then(res => res.json())
            .then((data: Property[]) => {
                console.log("Found properties:", data);
                setProperties(data); // Send data to map
            })
            .catch(err => console.error("Error fetching properties:", err));
    }
  };

  return (
    <div className="flex flex-col h-screen w-full">
      <div className="flex flex-row flex-1 overflow-hidden">
        <Sidebar 
          isPinMode={isPinMode}
          isConfirmMode={isConfirmMode}
          onPinClick={() => setIsPinMode(true)}
          onConfirmClick={handleConfirm}
          radius={radius}
          onRadiusChange={setRadius}
          hasActivePin={hasActivePin}
          onDeletePin={() => {
            setHasActivePin(false);
            setIsConfirmMode(false);
            setProperties([]); // Clear results if they delete the pin
            setSearchLocation(null);
          }}
        />

        <main className="flex-1 h-full relative">
          <GoogleMapsComponent 
            isPinMode={isPinMode}
            setPinMode={setIsPinMode}
            isConfirmMode={isConfirmMode}
            setIsConfirmMode={setIsConfirmMode}
            searchRadius={radius}
            hasActivePin={hasActivePin}
            setHasActivePin={setHasActivePin}
            
            // 3. PASS THE NEW PROPS
            properties={properties}
            onPinPositionChange={(lat, lng) => setSearchLocation({ lat, lng })}
          />
        </main>
      </div>
    </div>
  );
}