'use client';
import React from 'react';

interface SidebarProps {
  isPinMode: boolean;
  onPinClick: () => void;
  radius: number;
  onRadiusChange: (val: number) => void;
  hasActivePin: boolean;
  onDeletePin: () => void;
  // ADDED THESE TWO:
  isConfirmMode: boolean;
  onConfirmClick: () => void;
}

export default function Sidebar({ 
  isPinMode, 
  onPinClick, 
  radius, 
  onRadiusChange, 
  hasActivePin, 
  onDeletePin,
  isConfirmMode,
  onConfirmClick
}: SidebarProps) {
  return (
    <aside className="w-[350px] h-full bg-white border-r shadow-lg flex flex-col p-6 space-y-8 z-10">
      <div>
        <h2 className="text-2xl font-bold text-gray-800"></h2>
        <p className="text-sm text-gray-500 mt-1">Find empty properties by area</p>
      </div>

      {/* 1. THE RADIUS SLIDER */}
      <div className="flex flex-col space-y-4">
        <div className="flex justify-between items-center">
          <label className="text-sm font-semibold text-gray-700">Search Radius</label>
          <span className="bg-blue-100 text-blue-700 px-2 py-1 rounded text-xs font-bold">
            {radius} miles
          </span>
        </div>
        <input 
          type="range" 
          min="1" 
          max="50" 
          value={radius}
          onChange={(e) => onRadiusChange(Number(e.target.value))}
          className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer accent-blue-600"
        />
        <div className="flex justify-between text-[10px] text-gray-400 px-1">
          <span>1 mi</span>
          <span>50 mi</span>
        </div>
      </div>

      <hr className="border-gray-100" />

      {/* 2. ACTION BUTTONS */}
      <div className="flex flex-col gap-3">
        {!isConfirmMode ? (
          <button 
            onClick={onPinClick}
            className={`w-full py-3 rounded-xl font-bold shadow-md transition-all flex items-center justify-center gap-2 ${
              isPinMode ? 'bg-gray-400 text-white cursor-not-allowed' : 'bg-blue-600 text-white hover:bg-blue-700'
            }`}
            disabled={isPinMode}
          >
            {isPinMode ? 'Click on Map...' : 'üìç Place Pin on Map'}
          </button>
        ) : (
          <button 
            onClick={onConfirmClick}
            className="w-full py-3 rounded-xl font-bold bg-green-600 text-white shadow-md hover:bg-green-700 transition-all flex items-center justify-center gap-2"
          >
            <span>‚úÖ</span> Confirm Location
          </button>
        )}

        {hasActivePin && (
          <button 
            onClick={onDeletePin} 
            className="w-full py-2 rounded-xl font-semibold text-red-500 border border-red-200 hover:bg-red-50 transition-colors text-sm"
          >
            Delete Current Pin
          </button>
        )}
      </div>

      {/* 4. RESULTS PLACEHOLDER */}
      <div className="flex-1 border-t pt-4 overflow-hidden flex flex-col">
        <h3 className="text-sm font-semibold text-gray-400 uppercase tracking-wider mb-4">Results</h3>
        <div className="flex flex-col items-center justify-center flex-1 text-center text-gray-400 border-2 border-dashed border-gray-100 rounded-xl px-4">
          <p className="text-xs">
            {isConfirmMode 
              ? "Drag the pin to adjust, then click confirm to see results." 
              : "Place a pin to see properties in this radius."}
          </p>
        </div>
      </div>
    </aside>
  );
}